import styled, { keyframes } from "styled-components"
const dashXLarge = keyframes`
    0% {
        stroke-dasharray: 1, 248;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 177, 248;
        stroke-dashoffset: -75;
    }
    100% {
        stroke-dasharray: 177, 248;
        stroke-dashoffset: -246;
    }
`
const dashLarge = keyframes`
    0% {
        stroke-dasharray: 1, 141;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 100, 141;
        stroke-dashoffset: -43;
    }
    100% {
        stroke-dasharray: 100, 141;
        stroke-dashoffset: -139;
    }
`
const dashMedium = keyframes`
    0% {
        stroke-dasharray: 1, 91;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 65, 91;
        stroke-dashoffset: -28;
    }
    100% {
        stroke-dasharray: 65, 91;
        stroke-dashoffset: -89;
    }
`
const dashSmall = keyframes`
    0% {
        stroke-dasharray: 1, 72;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 51, 72;
        stroke-dashoffset: -22;
    }
    100% {
        stroke-dasharray: 51, 72;
        stroke-dashoffset: -70;
    }
`
const dashXSmall = keyframes`
    0% {
        stroke-dasharray: 1, 53;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 38, 53;
        stroke-dashoffset: -16;
    }
    100% {
        stroke-dasharray: 38, 53;
        stroke-dashoffset: -51;
    }
`
const dashXXSmall = keyframes`
    0% {
        stroke-dasharray: 1, 41;
        stroke-dashoffset: 0;
    }
    50% {
        stroke-dasharray: 29, 41;
        stroke-dashoffset: -12;
    }
    100% {
        stroke-dasharray: 29, 41;
        stroke-dashoffset: -39;
    }
`
const dashMap = {
  "x-large": dashXLarge,
  large: dashLarge,
  medium: dashMedium,
  small: dashSmall,
  "x-small": dashXSmall,
  "xx-small": dashXXSmall,
}
function attachThemeAttrs(styledElement) {
  return styledElement.attrs(props => {
    return {
      ...{
        size: 30,
        variant: "primary",
      },
      ...props,
    }
  })
}
const StyledArcSpinner = attachThemeAttrs(styled.svg)`
    animation: rotate 1s linear infinite;
    display: block;
    transform-origin: 50% 50%;
    width: ${props => props.size}px;
    height: ${props => props.size}px;
    & .path {
        stroke: var(--color-${props => props.variant});
        stroke-linecap: round;
        animation: ${props =>
          dashMap[props.size] || dashMap.medium} 1.5s ease-in-out infinite;
    }
    @keyframes rotate {
        100% {
            transform: rotate(360deg);
        }
    }
    ${props =>
      props.color !== null &&
      props.color !== undefined &&
      `
            & .path {
                stroke: ${props.color};
            }
        `};
`
const StyledWrap = styled.div`
  position: relative;
  min-width: 40px;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
`
export default function Arc(props) {
  return (
    <StyledWrap>
      <StyledArcSpinner
        viewBox={`${0} ${0} ${30} ${30}`}
        size={30}
        variant={"primary"}
        {...props}
      >
        <circle
          className="path"
          cx={30 / 2}
          cy={30 / 2}
          r={(30 - 3) / 2}
          fill="none"
          strokeWidth="3"
        />
      </StyledArcSpinner>
    </StyledWrap>
  )
}
